* {
  margin: 0;
  padding: 0;
  /* font-family: "Courier New", Courier, monospace; */
  /* font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif; */
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
li {
  transition: all 500ms;
  list-style-type: none;
}
img {
  cursor: pointer;
  transition: all 500ms;
}
body {
  overflow-x: hidden;
}
.dark {
  background-color: rgb(30, 30, 30);
  color: rgb(212, 212, 212);
}
.wrapper {
  width: 100vw;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.blur {
  filter: blur(5px);
}
.no-scroll {
  overflow: hidden;
}

/* HEADER */
.header {
  border-bottom: 1px solid #000;
  box-shadow: 1px 1px 8px #47536f;
  transition: all 500ms;
  z-index: 1;
  width: 100%;
  top: 0;
  display: flex;
  flex-direction: column;
  opacity: 1;

  z-index: 5;
}
.header:hover {
  box-shadow: 1px 1px 18px #47536f;
}
.header.dark {
  background-color: rgb(20, 20, 20);
}
.header__content {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;

  opacity: 1;
  transition: all 0.25s linear 0s;
}

button {
  background-color: #47536f;
  color: rgb(210, 218, 224);
  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
}

/* Scroll or Click to load more*/
.option-load {
  position: absolute;
  left: 30px;

  border-radius: 5px;

  color: rgb(255, 255, 255);
  cursor: pointer;
}
.option-load__text {
  padding: 5px 10px;
  text-align: center;
  display: block;
  font-size: 20px;

  background-color: #47536f;
  border-radius: 5px;
}
.checkbox {
  -webkit-appearance: none;
  appearance: none;
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.checkbox:focus + .option-load__text {
  box-shadow: 1px 1px 8px #47536f;
}

/* Theme */
.theme {
  position: absolute;
  /*!!!!! TEST !!!!!*/
  /* right: 130px; */
  right: 30px;

  display: flex;
  justify-content: center;
  align-items: center;

  cursor: pointer;
}
.theme__span {
}
.theme > span {
  font-size: 34px;
}
.checkbox:focus + .theme__span {
  border: 2px solid #47536f;
  border-radius: 50%;
}

/* Search Container */
.search-container {
  width: 50%;
  height: 80%;
  min-width: 390px;
}
.search-form {
  display: flex;

  height: 100%;
  width: 100%;
  outline: 1px solid #000;
  border-radius: 5px;
  overflow: hidden;
}
.search-input {
  width: 90%;
  border: none;
  padding-left: 10px;
  outline: none;
  font-size: 24px;
  transition: all 500ms;
  background: rgb(247, 247, 247);
}
.search-input:focus {
  box-shadow: inset 1px 1px 8px #47536f;
}
.search-btn {
  width: 10%;
  min-width: 50px;
  outline: 1px solid #000;
}
.search-btn:focus {
  box-shadow: inset 5px 5px 5px #63749c, inset -5px -5px 5px #63749c;
}
.search-btn > span {
  color: rgb(210, 218, 224);
  transform: scale(1.5);
}
.search-btn:focus > span {
  color: rgb(255, 255, 255);
}

/* CONTENT */
.content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
}

/* Requests */
.requests {
  width: 90vw;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px 0;
}
.requests > li {
  margin: 5px;
  padding: 3px 5px;
  font-size: 22px;
  border-radius: 5px;
  border: 1px solid #000;
  cursor: pointer;
}
.requests.dark > li {
  background-color: rgb(30, 30, 30);
  border: 1px solid #65779f;
}
.requests > li:hover {
  box-shadow: 1px 1px 8px #47536f;
}
.requests.dark > li:hover {
  box-shadow: 1px 1px 8px #65779f;
}

/* Load more button */
.load-more-btn {
  padding: 10px;
  display: none;
  min-width: 150px;
}
.load-more-btn > button {
  width: 100%;
  min-height: 50px;
  border-radius: 5px;
  font-size: 24px;

  margin-bottom: 30px;
  box-shadow: 1px 1px 8px #47536f;
  transition: all 500ms;
}
.load-more-btn > button:hover {
  box-shadow: 1px 1px 18px #47536f;
}
.load-more-btn > button:focus {
  outline: none;
  box-shadow: 1px 1px 18px #47536f;
}

/* Up button */
.up-btn {
  display: none !important;
  position: fixed;

  right: 25px;
  bottom: 25px;
  text-decoration: none;
  font-size: 50px !important;
  color: rgb(232, 232, 232) !important;
  text-shadow: 2px 2px 2px #47536f, 0 2px 2px #47536f, -2px 0 2px #47536f,
    0 -2px 2px #47536f !important;

  transition: all 1s;

  background-color: #47536f6f;
  border-radius: 50%;
}
.up-btn:hover {
  transform: scale(1.5);
}

.active {
  display: block !important;
}

/* Handlebars */
.gallery {
  display: inline-flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 90%;
}
.gallery-item {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  margin-bottom: 40px;
  box-shadow: 1px 1px 8px #47536f;
  transition: all 500ms;
  padding: 20px 20px 0px 20px;
}
.gallery-item:hover {
  transform: scale(1.1);
  box-shadow: 1px 1px 18px #47536f;
}
.picture-container {
  width: 350px;
  height: 250px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.picture-container > img {
  object-fit: contain;
  display: block;
  width: 100%;
  height: auto;
}
.picture-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 50px;
  width: 100%;
}
.picture-info__item {
  display: flex;
  align-items: center;
  height: 30px;
}
.material-icons {
  margin-right: 5px;
  color: #47536f;
}

/* basicLightbox */
.basicLightbox {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.basicLightbox__placeholder > img {
  width: 90vw;
}

@media (max-width: 1100px) {
  .header {
    height: 70px;
    padding-top: 60px;
  }

  .option-load {
    top: 10px;
  }

  .search-container {
    position: relative;
    bottom: 0;
    width: 95%;
    min-width: 250px;
  }
  .theme {
    top: 10px;
  }
}
@media (max-width: 550px) {
  .header {
    height: 80px;
    padding-top: 70px;
  }
  .option-load {
    max-width: 150px;
  }
  .theme {
    top: 25px;
  }

  .picture-container {
    width: 300px;
    height: 200px;
  }
}
@media (max-width: 370px) {
  .option-load {
    left: 7px;
  }
  .theme {
    right: 7px;
  }
  .gallery-item {
    margin-bottom: 20px;
    width: 200px;
    min-height: 170px;
    padding: auto;
  }
  .picture-container {
    width: 195px;
    height: auto;
  }
  .picture-info {
    flex-wrap: wrap;
  }
}
